import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'wordColor'
})
export class WordColorPipe implements PipeTransform {

  transform(input: string, word): any {
    if (!word) return input;
    let result = (input+'').replace(word, `<span style="color: #6ca4ff;" class="tt">${word}</span>`)
    return result;
  }

}
