import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NavController } from '@ionic/angular'
import { Location } from '@angular/common'

import { products } from '../tab2/products'
import { CartService } from '../services/cart.service'

@Component({
    selector: 'app-detail',
    templateUrl: 'detail.page.html',
    styleUrls: ['detail.page.scss']
})

export class DetailPage {
    products = products;
    product: object;
    htmlStr: string;
    @Input() inputStr = '';
    emotion: string = 'happy';
    hero: object = {
        name: ''
    };
    // 给事件取别名
    @Output() myClick = new EventEmitter()

    constructor(
        private route: ActivatedRoute,
        private cartService: CartService,
        private nav: NavController,
        private location: Location
    ) {

    }

    ngOnInit() {
        this.route.paramMap.subscribe(param => this.product = this.products[param.get('id')])
        this.route.snapshot.paramMap.get('id')
        // console.log(this.route.snapshot.paramMap.get('id'), this.product)
        this.htmlStr = `
            <h1>innerHtml内容</h1>
            <h2 style="color: #1b75bb;">样式自定义</h2>
        `

    }

    addToCart(item) {
        this.cartService.addToCart(item)
        this.nav.navigateForward('/cart')
        // this.location.forward()
    }

    toUpper(e) {
        this.inputStr = e.toUpperCase()
    }

    getPhone(phone) {
        console.log(phone)
    }

    onSubmit(form) {
        console.log(form)
    }

    tapEmail(val) {
        console.log(val)
    }

}